body {
  background-color: palegreen;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#quote-box {
  border: 1px solid black;
  height: 40vh;
  width: 80vw;
  margin: 4rem auto;
  padding: 4rem 2rem 0 2rem;
}

#text {
  text-align: center;
  margin-bottom: 2rem;
}

#author {
  text-align: center;
  margin-bottom: 2rem;
}

#new-quote {
  display: block;
}

#tweet-quote {
  display: block;
}

#btn-box {
  display: flex;
  justify-content: space-around;
  padding-left: 8rem;
  padding-right: 8rem;
}